<template>	
	<div id="menu">
		<div class="menu-list">
			
			<router-link :to="'/latest?aid='+ menu.id" v-for="menu in menuData" :key="menu.id" class="btn" active-class="active" exact>{{menu.name}}</router-link>
		</div>
		
	</div>
</template>


<script>
import axios from 'axios'
	export default{
		name: 'mymenu',
		data(){
			return {
				menuData: []
			}
		},
		methods:{
			getData(){

				var self = this;
				axios.get('/zhihu/api/4/themes')
					.then(function(response){
						self.menuData = response.data.others;
						var obj = {"color":0,"thumbnail":"","description":"","id":0,"name":"今日新闻"}
						self.menuData.unshift(obj)
					})
					.catch(function(error){

					})
			}
		},
		created(){
			this.getData();
		}
	}

</script>

<style lang="scss">
	#menu{
		background: #f4f5f6;
	    height: 3.7rem;
	    -webkit-box-sizing: border-box;
	    box-sizing: border-box;
	    width: 100%;
	    position: fixed;

		.menu-list{
			overflow: hidden;
		    overflow-x: scroll;
		    -webkit-overflow-scrolling: touch;
		    white-space: nowrap;


			.btn{
				white-space: nowrap;
			    display: inline-block;
			    padding-left: 1rem;
			    padding-right: 1rem;
			    color: #505050;
			    text-decoration: none;
			    font-size: 1.7rem;
			    line-height: 2.6rem;
			    height: 2.6rem;
			    margin-left: .5rem;
			    margin-top: .5rem;
			    margin-bottom: .5rem;
			    -webkit-tap-highlight-color: rgba(0, 0, 0, 0.3);
			}

			.active{
				color: #da185a;
			}

			&::-webkit-scrollbar {
		        display: none;
		    }
		}
	}
</style>


