<template>
	<div id="latest">
		<ul class="stories">
			<li v-for="(item,index) in stories" @click="getNewsContent(item.id)">
				<img :src="getImage(item.images)">
				<h1>{{item.title}}
				</h1>
			</li>
		</ul>
	</div>
</template>



<script>
import axios from 'axios'
import store from '../store'

	export default {
		name: 'latest',
		data() {
			return {
				stories: [],
				storyExtra: []
			}
		},
		methods: {
			getImage(url){

                // 把现在的图片连接传进来，返回一个不受限制的路径
                if(url !== undefined){
                     return url[0].replace(/http\w{0,1}:\/\/p/g,'https://images.weserv.nl/?url=p');
                }
            },
            getNewsContent(id){
            	/*this.storiesId = id;*/
            	this.$router.push({ path: this.$route.fullPath, query: { sid: id }})
            	store.commit("setLatestContent")
            },
            getData(id){
            	let _self = this; let url = '';
            	(id == 0 || id == undefined) ? url = '/news/latest' : url = '/theme/'+id ;
				axios.get('/zhihu/api/4'+url)
					.then(function(response){
						_self.stories = response.data.stories;
						
						
					})
					.catch(function(error){
						
						if (error.response) {
							console.error("Error",error.response)
						}
					})
            }
		},
		created(){
		
			this.getData(this.$route.query.aid);
			
		},
		watch: {
            '$route'(to, from){
                this.getData(to.query.aid)
            },
            stories (cur,old) {
            	
            }
        }

	}
</script>

<style lang="scss">

	#latest{
		position: fixed;
		width: 100%;
		top: 7.7rem;
		bottom: 0;
		left: 0;
		overflow: auto;
		z-index: 99;

	}
	ul.stories{
		padding: 0;
		
		li{
			display: flex;
			flex: 1;
			border-bottom: 1px solid #f8f8f8;
			cursor:pointer;
			

			h1{
				display: flex;
				flex: 1 1 auto;
				font-size: 1.8rem;
				text-align: left;
				padding: .5rem 1rem;
				font-weight: normal;
			}

			img{
				
				width: 6rem;
				height: 6rem;
				min-width: 6rem;
				padding: .5rem;
			}
		}
	}

</style>


