<template>
	<div id="comments">
		<div class="header-top">
			<span class="back" @click="closeComments()">返回</span>
			评论

		</div>
		<div class="comments-content">
			<div id="longComments" class="comments">
				<h1 class="comments-title">长评论：{{longComments.length}}条</h1>
				<ul class="comments-list">
					<li v-for="long in longComments">
						<img :src="long.avatar">
						<div class="comments-list-body">
							<h2 class="comments-list-body-author">{{long.author}}</h2>
							<p class="comments-list-content">{{long.content}}</p>
							<div class="comments-list-footer">
								<span class="comments-time">{{long.time}}</span>
								<span class="comments-likes">{{long.likes}}</span>
							</div>
						</div>
					</li>
				</ul>
			</div>
			<hr>
			<div id="shortComments" class="comments">
				<h1 class="comments-title">短评论：{{shortComments.length}}条</h1>
				<ul class="comments-list">
					<li v-for="short in shortComments">
						<img :src="short.avatar">
						<div class="comments-list-body">
							<h2 class="comments-list-body-author">{{short.author}}</h2>
							<p class="comments-list-content">{{short.content}}</p>
							<div class="comments-list-footer">
								<span class="comments-time">{{short.time}}</span>
								<span class="comments-likes">{{short.likes}}</span>
							</div>
						</div>
					</li>
				</ul>
			</div>
		</div>
		
	</div>
</template>

<script>
import axios from 'axios'
import store from '../store'

export default{
	name: 'Comments',
	data(){
		return {
			longComments: [],
			shortComments: []
		}
	},
	methods: {
		getData(id){
			var self = this;
			axios.all([this.getLongComments(id), this.getShortComments(id)])
				.then(axios.spread(function(long,short){
					self.longComments = long.data.comments;
					self.shortComments = short.data.comments;
				}))
				.catch(function(){

				})
		},
		getLongComments (id){

			return axios.get('/zhihu/api/4/story/'+id+'/long-comments')
		},
		getShortComments (id){
			return axios.get('/zhihu/api/4/story/'+id+'/short-comments')
		},
		closeComments (){
			store.commit("setShowComments");
		},
	},
	mounted(){
        if(this.$route.query.sid){
        	 this.getData(this.$route.query.sid)
           
        }
    }
}
</script>

<style lang="scss">
	
	#comments{
		position: fixed;
		width: 100%;
		height: 100%;
		overflow: auto;
		top: 0;
		left: 0;
		z-index: 9999;
		background: #fff;

		.comments-content{
			position: fixed;
			width: 100%;
			top: 4rem;
			bottom: 0;
			overflow: auto;

		}


		.comments{
			overflow: hidden;
			padding: 0rem 0 0rem 1rem;

			.comments-title{
				text-align: left;
			}

			.comments-list{
				padding: 0;

				li{
					display: flex;

					img{
						width: 5rem;
						height: 5rem;
						min-width: 5rem;
						border-radius: 50%;


					}

					.comments-list-body{
						flex: 1 1 auto;
						margin-left: 1rem;
						border-bottom: 1px solid #ddd;
						padding-right: 1rem;
						padding-bottom: 1rem;
						margin-bottom: 1rem;

						.comments-list-body-author{
							text-align: left;
						}

						.comments-list-content{
							margin: .5rem 0 !important;
							text-align: left;
						}

						.comments-list-footer{
							margin-top: 1rem;

							.comments-time{
								float: left;
							}

							.comments-likes{
								float:right;

							}
						}

					}




				}
			}
		}

		
	}
</style>


