<template>
  <div id="app">
    <header class="header-top">知乎日报</header>
    <menu-btn></menu-btn>  
    <router-view/>

    <latestContent  v-if="showLatestContent"></latestContent>
    <comments v-if="showComments"></comments>
  </div>
</template>

<script>
import menu from '@/components/menu.vue'
import latestContent from '@/components/latestContent'
import comments from '@/components/comments'
import store from './store'
export default {
  name: 'App',
  components:{
    menuBtn: menu,
    latestContent,
    comments
  },
  computed:{
    showLatestContent(){
      return this.$store.getters.getLatestContent
    },
    showComments(){
      return this.$store.getters.getShowComments
    }
  }
}
</script>



<style>
*{
  margin: 0;
  padding: 0;
}
ul {
  list-style: none;
}
html{
  font-size: 10px;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  font-size: 1.4rem;
}
.header-top {
  width: 100%;
  height: 4rem;
  line-height: 4rem;
  background: #da185a;
  font-size: 2rem;
  color: #fff;
}
.back{
  float: left;
  margin-left: 1rem;
  font-size: 1.6rem;
}
.share{
  float: right;
  margin-right: 1rem;
  font-size: 1.6rem;
}
.headline{
  display: none;
}
</style>



