const latest={
	state:{
		showLatestContent: false,  //文章详细页显示
		showComments: false //评论显示
		
	},
	getters:{
		getLatestContent: state => state.showLatestContent,
		getShowComments: state => state.showComments

	},
	mutations:{
		setLatestContent: state => state.showLatestContent ? state.showLatestContent = false : state.showLatestContent = true,
		setShowComments: state => state.showComments ? state.showComments = false : state.showComments = true

	},
	actions:{

	}
}

export default latest


