import Vue from 'vue'
import Router from 'vue-router'
/*import HelloWorld from '@/components/HelloWorld'*/
import Latest from '@/components/latest'
 
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/latest?aid=0'
    },{
      path: '/latest',
      name: 'Latest',
      component: Latest,
      children: [
        {
          path: '/latest/?aid=0',
          component: Latest
        }
      ]
    }

  ]
})



