<template>
	<div class="latestContent">
		<div class="header-top">
			<h1 class="article-title" :title="art.title">{{art.title}}</h1>
			<span class="back" @click="hide()">返回</span>
			<span class="share" @click="share()">分享</span>
		</div>
		<div class="article" v-html="art.body">
			<div>{{art.body}}</div>
		</div>
		<div class="storyExtra">
			<span>
				点赞 {{storyExtra.popularity}}
			</span>
			<!-- <span>没有帮助</span>
			<span>感谢</span>
			<span>收藏</span> -->
			<span @click="openComments()">
				评论 {{storyExtra.comments}}
			</span>
		</div>
	</div>
</template>


<script>
import axios from 'axios'
import store from '../store'
	export default{
		name: 'latestContent',
		data(){
			return {
				art:[],
				storyExtra: {}
			}
		},
		methods:{
			getData(id){
				var self = this;
				self.art = [];

				axios.all([this.getArticle(id),this.getComments(id)])
				.then(axios.spread(function (Article, Comments) {
				    // 两个请求现在都执行完成
				    self.art = Article.data;
				    self.storyExtra = Comments.data;

				  }))
				.catch(function(error){
					
					if (error.response) {
						console.error("Error",error.response)
					}
				})
			},
			getArticle(id){ //新闻内容
				return axios.get('/zhihu/api/4/news/'+ id);
			},
			getComments(id){//评论
				return axios.get('/zhihu/api/4/story-extra/'+ id);
			},
			hide(){
				store.commit("setLatestContent");
				this.$router.back(-1);
			},
			openComments(){
				store.commit("setShowComments");
			},
			share(){

			}
		},
        watch:{
            '$route'(to, from){
                if(to.query.sid){
                	 this.getData(to.query.sid)
                }
            }
        },
        mounted(){
            if(this.$route.query.sid){
            	 this.getData(this.$route.query.sid)
               
            }
        }/*,
		
		mounted() {
			
				if(this.$store.getters.getLatestContent){

					this.getData();
				}
				
				
			
			
		}*/
	}
</script>
<style> 
  @import '../../static/css/style.css'
</style>
<style lang="scss">
	.latestContent{
		width: 100%;
		height: 100%;
		position: fixed;
		top: 0;
		left: 0;
		background: #fff;
		overflow: auto;
		z-index: 999;

	}
	.article-title{
		width: 70%;
		height: 4rem;
		line-height: 4rem;
		position: absolute;
		margin-top: 0;
		left: 50%;
		transform: translateX(-50%);
		overflow: hidden;
		text-overflow:ellipsis;
		white-space: nowrap;

	}
	.article{
		position: fixed;
		overflow: auto;
		top: 4rem;
		bottom: 4rem;
		width: 100%;

	}

	.storyExtra{
		position: fixed;
		width: 100%;
		height: 4rem;
		bottom: 0;
		display: flex;


		span{
			flex: 1;
			align-items: center;
        	justify-content: center;
        	display: flex;
		}
	}

</style>


